import { cn } from '@/lib/utils';
import React from 'react';
interface IconProps {
    text: string;
    className?: string;
}

const UnderlineHeading = ({ text, className }: IconProps) => {
    return (
        <div className="border-b border-primery-blue-900 text-primery-blue-900 pb-[5.5px] w-fit">
            <h2
                className={cn('font-cairo font-bold text-[22px] leading-[33px] tracking-normal text-center', className)}
            >
                {text}
            </h2>
        </div>
    );
};

export default UnderlineHeading;
